"use client";

import React, { useEffect, useRef, useState } from "react";
import Matter from "matter-js";
import { useTheme } from "@/lib/theme-context";
import { useWindowSize } from "@/hooks/use-window-size"; // I might need to create this hook or just use window directly inside effect

const COLORS = {
    neon: "#00ffcc",
    red: "#ff0055",
    blue: "#00ccff",
};

interface PhysicsHeroProps {
    onBookingTrigger: () => void;
    studioName?: string;
    appealPoint?: string;
}

export const PhysicsHero: React.FC<PhysicsHeroProps> = ({ onBookingTrigger, studioName, appealPoint }) => {
    const sceneRef = useRef<HTMLDivElement>(null);
    const engineRef = useRef<Matter.Engine | null>(null);
    const { primaryColor, gravityStrength, backgroundImage, backgroundColor, logoUrl } = useTheme();

    // Track if we already triggered to avoid spamming
    const hasTriggeredRef = useRef(false);

    useEffect(() => {
        if (!sceneRef.current) return;

        // Module aliases
        const Engine = Matter.Engine,
            Render = Matter.Render,
            Runner = Matter.Runner,
            Bodies = Matter.Bodies,
            Composite = Matter.Composite,
            Mouse = Matter.Mouse,
            MouseConstraint = Matter.MouseConstraint,
            Events = Matter.Events;

        // Create engine
        const engine = Engine.create();
        engineRef.current = engine;

        // Set gravity (initial)
        engine.gravity.y = gravityStrength;

        // Create renderer
        const width = sceneRef.current.clientWidth;
        const height = sceneRef.current.clientHeight;

        const render = Render.create({
            element: sceneRef.current,
            engine: engine,
            options: {
                width,
                height,
                background: "transparent",
                wireframes: false, // Solid shapes
                showAngleIndicator: false,
            },
        });

        // Create bodies
        // 1. Walls
        const wallOptions = { isStatic: true, render: { fillStyle: "#333" } };
        const ground = Bodies.rectangle(width / 2, height + 30, width, 60, wallOptions);
        const leftWall = Bodies.rectangle(-30, height / 2, 60, height, wallOptions);
        const rightWall = Bodies.rectangle(width + 30, height / 2, 60, height, wallOptions);

        // 2. Cart Area (Sensor)
        // Located at bottom center
        const cartSensor = Bodies.rectangle(width / 2, height - 50, 200, 100, {
            isStatic: true,
            isSensor: true, // Doesn't physically collide but detects overlaps
            label: "CartSensor",
            render: {
                fillStyle: "rgba(255, 255, 255, 0.1)",
                strokeStyle: "#fff",
                lineWidth: 2,
            },
        });

        // 3. Falling items (Studio Equipments)
        const items = [];
        const itemOptions = (label: string) => ({
            restitution: 0.5,
            render: {
                fillStyle: COLORS[primaryColor as keyof typeof COLORS] || "#fff",
                strokeStyle: "#fff",
                lineWidth: 1
            },
            label: label,
        });

        // Add some random shapes representing amps, guitars (abstractly)
        // Box (Amp)
        items.push(Bodies.rectangle(width / 2 - 50, 0, 60, 60, itemOptions("Amp")));
        // Circle (Drum)
        items.push(Bodies.circle(width / 2, -100, 30, itemOptions("Drum")));
        // Trapezoid (Speaker)
        items.push(Bodies.trapezoid(width / 2 + 50, -200, 60, 80, 0.5, itemOptions("Speaker")));

        Composite.add(engine.world, [ground, leftWall, rightWall, cartSensor, ...items]);

        // Add Mouse Interaction
        const mouse = Mouse.create(render.canvas);
        const mouseConstraint = MouseConstraint.create(engine, {
            mouse: mouse,
            constraint: {
                stiffness: 0.2,
                render: {
                    visible: false,
                },
            },
        });

        Composite.add(engine.world, mouseConstraint);

        // Keep the mouse in sync with rendering
        render.mouse = mouse;

        // Running
        Render.run(render);
        const runner = Runner.create();
        Runner.run(runner, engine);

        // Collision Detection for Cart
        Events.on(engine, "collisionStart", (event) => {
            const pairs = event.pairs;
            for (const pair of pairs) {
                const { bodyA, bodyB } = pair;
                if (bodyA.label === "CartSensor" || bodyB.label === "CartSensor") {
                    const otherBody = bodyA.label === "CartSensor" ? bodyB : bodyA;
                    if (!otherBody.isStatic) { // Ensure it's an item
                        if (!hasTriggeredRef.current) {
                            onBookingTrigger();
                            hasTriggeredRef.current = true;

                            // Optional: visual feedback or reset trigger after delay
                            setTimeout(() => { hasTriggeredRef.current = false; }, 2000);
                        }
                    }
                }
            }
        });

        // Handle Resize (Basic)
        const handleResize = () => {
            if (!sceneRef.current) return;
            render.canvas.width = sceneRef.current.clientWidth;
            render.canvas.height = sceneRef.current.clientHeight;
        };
        window.addEventListener('resize', handleResize);

        return () => {
            Render.stop(render);
            Runner.stop(runner);
            if (render.canvas) {
                render.canvas.remove();
            }
            window.removeEventListener('resize', handleResize);
        };
    }, []); // Only run once on mount

    // Update Gravity and Colors when theme changes
    useEffect(() => {
        if (engineRef.current) {
            engineRef.current.gravity.y = gravityStrength;

            // Update colors of non-static bodies (the items)
            // Note: This is an expensive operation in loop, but fine for few items
            const bodies = Matter.Composite.allBodies(engineRef.current.world);
            bodies.forEach(body => {
                if (!body.isStatic && body.label !== "Mouse Body") {
                    body.render.fillStyle = COLORS[primaryColor as keyof typeof COLORS];
                }
            });
        }
    }, [gravityStrength, primaryColor]);

    return (
        <div
            className="relative w-full h-[600px] overflow-hidden border-b border-white/10"
            style={{
                backgroundColor: backgroundColor,
                backgroundImage: backgroundImage ? `url(${backgroundImage})` : undefined,
                backgroundSize: 'cover',
                backgroundPosition: 'center'
            }}
        >
            <div
                ref={sceneRef}
                className="absolute inset-0 w-full h-full z-0"
            />

            {/* Overlay Text */}
            <div className="absolute inset-0 flex flex-col items-center justify-center text-center z-10 pointer-events-none select-none p-4">
                {logoUrl ? (
                    <img src={logoUrl} alt="Studio Logo" className="h-32 mb-6 opacity-90 drop-shadow-[0_0_20px_rgba(255,255,255,0.5)] object-contain" />
                ) : (
                    <></>
                )}

                <h1 className="text-5xl md:text-8xl font-bold text-white tracking-tighter mb-6"
                    style={{ textShadow: `0 0 30px ${COLORS[primaryColor]}` }}>
                    {studioName || "予約受付"}
                </h1>

                {appealPoint && (
                    <p className="text-gray-200 text-lg md:text-2xl font-sans bg-black/40 backdrop-blur-md px-8 py-4 rounded-full border border-white/10 shadow-2xl animate-in fade-in slide-in-from-bottom-4 duration-1000">
                        {appealPoint}
                    </p>
                )}
            </div>

            {/* Visual Decor Only - Removed Cart Indicator */}
        </div>
    );
};
