"use client";

import React, { useState, useEffect } from "react";
import { useParams } from "next/navigation";
import { ThemeProvider, useTheme } from "@/lib/theme-context";
import { PhysicsHero } from "@/components/PhysicsHero";
// Removed SettingsPanel
// Removed BookingModal usage here (users select room first)
import { fetchStudio } from "@/actions/studio";
import Link from "next/link";
import { Button } from "@/components/ui/button";

export default function StudioPageWrapper() {
    return (
        <ThemeProvider>
            <StudioPageContent />
        </ThemeProvider>
    );
}

function StudioPageContent() {
    const params = useParams();
    const id = params.id as string;

    const { profile } = useLineLiff();
    const { setLogoUrl, setBackgroundImage } = useTheme();

    const [studio, setStudio] = useState<any>(null);

    useEffect(() => {
        const load = async () => {
            if (!id) return;
            const data = await fetchStudio(id);
            if (data) {
                setStudio(data);
                // setLogoUrl(data.logoUrl);
            }
        };
        load();
    }, [id, setLogoUrl, setBackgroundImage]);

    if (!studio) return <div className="min-h-screen bg-black flex items-center justify-center text-white">Loading Studio...</div>;

    return (
        <main className="min-h-screen bg-black text-white selection:bg-cyan-500 selection:text-black">
            {/* Header / Nav */}
            <header className="absolute top-0 w-full p-6 flex justify-between items-center z-50 pointer-events-none px-10">
                <div /> {/* Spacer for flex-between */}
                <div className="flex gap-4 items-center pointer-events-auto">
                    {/* Schedule removed from here. Accessed via Room selection only. */}
                    <div className="text-sm font-mono tracking-widest opacity-50">
                        {profile ? `Player: ${profile.displayName}` : "Guest Mode"}
                    </div>
                </div>
            </header>

            {/* Physics Hero Area - Visual Only now, or just fun interaction */}
            <div className="relative">
                <PhysicsHero
                    studioName={studio.storeName}
                    appealPoint={studio.appealPoint}
                    // No modal trigger here anymore
                    onBookingTrigger={() => console.log("Physics interaction")}
                />
            </div>

            {/* Content Section: Rooms List */}
            <section className="container mx-auto px-4 py-20 space-y-10">
                <div className="text-center mb-10">
                    <h2 className="text-3xl font-bold mb-4">STUDIO ROOMS</h2>
                    <p className="text-gray-400">ご希望のスタジオ（部屋）を選択して予約してください</p>
                </div>

                <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-8">
                    {studio.rooms?.length > 0 ? studio.rooms.map((room: any, i: number) => (
                        <div key={i} className="group relative overflow-hidden rounded-xl border border-white/10 bg-slate-900/50 hover:border-cyan-500 hover:shadow-[0_0_30px_rgba(6,182,212,0.3)] transition-all duration-300">
                            {/* Room Image if available */}
                            {room.images && room.images.length > 0 ? (
                                <div className="h-48 w-full relative">
                                    <img src={room.images[0]} alt={room.name} className="object-cover w-full h-full opacity-70 group-hover:opacity-100 transition-opacity" />
                                    <div className="absolute inset-0 bg-gradient-to-t from-black to-transparent" />
                                </div>
                            ) : (
                                <div className="h-32 w-full bg-gradient-to-br from-slate-800 to-black" />
                            )}

                            <div className="p-6">
                                <h3 className="text-2xl font-bold mb-2 text-white group-hover:text-cyan-400 flex justify-between items-center">
                                    {room.name}
                                </h3>

                                {room.description && (
                                    <p className="text-gray-400 text-sm mb-4 line-clamp-2">{room.description}</p>
                                )}

                                <div className="space-y-1 text-sm text-gray-500 font-mono mb-6">
                                    <div className="flex justify-between">
                                        <span>平日</span>
                                        <span className="text-gray-300">¥{room.pricing?.weekday?.slots?.[0]?.price || 0}/h~</span>
                                    </div>
                                    <div className="flex justify-between">
                                        <span>土日祝</span>
                                        <span className="text-gray-300">¥{room.pricing?.saturday?.slots?.[0]?.price || 0}/h~</span>
                                    </div>
                                </div>

                                <Link href={`/studios/${id}/rooms/${i}`} className="block">
                                    <Button className="w-full bg-cyan-900/20 border border-cyan-500/50 text-cyan-400 hover:bg-cyan-500 hover:text-black hover:border-cyan-500 font-bold tracking-widest transition-all">
                                        予約・空き状況 &gt;
                                    </Button>
                                </Link>
                            </div>
                        </div>
                    )) : (
                        <div className="col-span-full text-center text-gray-500">ルーム情報なし</div>
                    )}
                </div>
            </section>

            <footer className="border-t border-white/10 py-10 text-center text-gray-500 text-sm">
                © 2026 {studio.storeName}
            </footer>
        </main>
    );
}
